import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Logo from '../Images/Logo.png'
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Avatar from '@material-ui/core/Avatar';
import Menu from '@material-ui/core/Menu';
import Switch from '@material-ui/core/Switch';

//Components
import theme from '../theme';
import CardLecture from './CardLecture'
import Lecture from './Lecture';
import About from './About'
import Favorite from './Favorites'
//import Book from '../data/stories.json';

//firebase
import firebase from "firebase";

// css
import '../css/responsive.css'

class MainConcent extends React.Component {
   
        state = {
            auth: true,
            anchorEl: null,
            styleSelected:true,
            currentLecture:null,
            Book:[],
            likes:[],
            userAccount:null,
            showAbout:false,
            showFavorite:false
        };

      
        
      handleChange(event){
        this.setState({ auth: event.target.checked });
      };
      handleChangeStyle(event){
        this.setState({ styleSelected: event.target.checked });
      };
    
      handleMenu(event){
        this.setState({ anchorEl: event.currentTarget });
      };
    
      handleClose(){
          this.setState({ anchorEl: null });
      };
      selectLecture(currentLecture){
        this.setState({currentLecture,showAbout:false,showFavorite:false});
      }
      getAbout(){
        this.setState({showAbout:true});
      }
      getFavorite(){
        this.setState({showFavorite:true,anchorEl: null});
      }
      renderCards(){
        var Book=this.state.Book;
        if(Book.length>0)
            return Book.map(story=>{
                return(
                    <CardLecture {...story} bookid={story.key} likeLecture={this.setLike.bind(this)} selectLecture={this.selectLecture.bind(this)} liked={this.state.likes} disabledlikes={this.state.userAccount==null?true:false}/>
                )
            });

      }
      loginFacebook(){
        this.setState({anchorEl: null});
        var provider = new firebase.auth.FacebookAuthProvider();
        firebase.auth().signInWithPopup(provider).then((result)=> {
            // This gives you a Facebook Access Token. You can use it to access the Facebook API.
            // The signed-in user info.
            var userAccount = result.user;
            this.saveUserInformation(result);
            this.setState({userAccount,anchorEl: null});
            this.getLikes(userAccount.uid)
            // ...
          }).catch((error)=> {
            console.warn(error)
            // Handle Errors here.
            var errorCode = error.code;
            var errorMessage = error.message;
            // The email of the user's account used.
            var email = error.email;
            // The firebase.auth.AuthCredential type that was used.
            var credential = error.credential;
            // ...
            this.setState({userAccount:null,anchorEl: null});
          });
      }

      sessionPersist(){

        firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL)
        .then(function() {
          // Existing and future Auth states are now persisted in the current
          // session only. Closing the window would clear any existing state even
          // if a user forgets to sign out.
          // ...
          // New sign-in will be persisted with session persistence.
          return this.loginFacebook();
        })
        .catch(function(error) {
          // Handle Errors here.
          var errorCode = error.code;
          var errorMessage = error.message;
        });
      }
      saveUserInformation(userInformation){
       var logindate = new Date();
       var userId=userInformation.user.uid+"_"+(logindate/1);
       firebase.database().ref('/users/'+userId).set(
         {
         "info":JSON.stringify(userInformation)      
        }
         , (error)=> {
          if (error) {
            console.error(error)
          } 
        });
      }

      logOut(){
        this.setState({ anchorEl: null,userAccount:null });
      }
      initFirebase(){
                  // Initialize Firebase
        var config = {
            apiKey: "AIzaSyDcdo6A5ux_wNtafiEYB7A_0vxUxsRaxj8",
            authDomain: "gamereading.firebaseapp.com",
            databaseURL: "https://gamereading.firebaseio.com",
            projectId: "gamereading",
            storageBucket: "gamereading.appspot.com",
            messagingSenderId: "493040980527"
        };
        firebase.initializeApp(config);
      }
      getStories(){
        return firebase.database().ref('/stories').once('value').then((Stories)=> {
            this.setState({Book:Stories.val()});
        // ...
        });
      }
      getLikes(userId){
        return firebase.database().ref('/likes/'+userId).once('value').then((Stories)=> {
            this.setState({likes:Stories.val().book});
        // ...
        });
      }
      setLike(bookid){
        let booksLikes=this.state.likes
        let userId=this.state.userAccount.uid
        if (booksLikes.find(book=>{return book==bookid})==bookid)
            booksLikes= booksLikes.filter(book=>{return book!=bookid})
        else
            booksLikes.push(bookid)

        firebase.database().ref('/likes/'+userId).set({
          "book": booksLikes
        }, (error)=> {
          if (error) {
            console.error(error)
          } else {
            this.setState({likes:booksLikes});
          }
        });
      }
      
    componentDidMount(){
        this.initFirebase();  
        this.getStories();      
    }
    render(){
        const {styles}=theme;
        const open = Boolean(this.state.anchorEl);
        return (
        <>
            <div style={this.state.styleSelected?styles.darkStyleBackgroud:styles.whiteStyleBackgroud}>
              
                        <AppBar position="static" style={styles.AppBar}>
                            <Toolbar className="toolbar">
                                <img src={Logo} />
                                <Typography variant="h5" color={this.state.styleSelected?"inherit":"default"} style={styles.grow}>
                                GameReading
                                </Typography>
                                <Button color={this.state.styleSelected?"inherit":"default"}  onClick={()=>this.selectLecture(null)}>Lecturas</Button>
                                <Button color={this.state.styleSelected?"inherit":"default"} onClick={()=>this.getAbout()}>Acerca del proyecto</Button>
                                <Switch
                                    checked={this.state.styleSelected}
                                    onChange={this.handleChangeStyle.bind(this)}
                                    value="styleSelected"
                                    color="default"
                                />
                                {this.state.auth && (
                                <div>
                                    <IconButton
                                    aria-owns={open ? 'menu-appbar' : undefined}
                                    aria-haspopup="true"
                                    onClick={this.handleMenu.bind(this)}
                                    color={this.state.styleSelected?"inherit":"default"}
                                    >
                                    {this.state.userAccount==null?<AccountCircle />:<Avatar alt="Lector" src={this.state.userAccount.photoURL} />}
                                    
                                    </IconButton>
                                    {
                                      this.state.userAccount==null?
                                    <Menu
                                    id="menu-appbar"
                                    anchorEl={this.state.anchorEl}
                                    anchorOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    open={open}
                                    onClose={this.handleClose.bind(this)}
                                    >
                                    
                                      <MenuItem onClick={this.loginFacebook.bind(this)}>Iniciar Sesión</MenuItem>
                                                                 
                                    </Menu>
                                    :
                                    <Menu
                                    id="menu-appbar"
                                    anchorEl={this.state.anchorEl}
                                    anchorOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    open={open}
                                    onClose={this.handleClose.bind(this)}
                                    >
                                    <MenuItem disabled={true}> {this.state.userAccount.displayName}</MenuItem>
                                      <MenuItem onClick={()=>this.getFavorite()}>Favoritos</MenuItem>
                                      <MenuItem onClick={this.logOut.bind(this)}>Salir</MenuItem>
                                      </Menu>
                                    }
                                </div>
                                )}
                            </Toolbar>
                        </AppBar>
                     
                        <div className="contenedor">
                                {
                                    this.state.showAbout==true?<About/>:
                                    this.state.showFavorite==true?<Favorite books={this.state.Book} likes={this.state.likes} selectLecture={this.selectLecture.bind(this)} userName={this.state.userAccount.displayName}/>:
                                    this.state.currentLecture==null?this.renderCards():
                                    <div><Lecture currentLecture={this.state.currentLecture} user={this.state.userAccount} color={this.state.styleSelected?"inherit":"default"}/></div> 
                                }
                      </div>
            </div>
            <div className="footer">
              <Typography variant="caption" color={this.state.styleSelected?"inherit":"default"}>
                      <p> <strong> Aplicación desarrollada por Helder Castrillón </strong></p>
                      <p> Grupo de Invesigación Tic Unicomfacauca</p>
                      <p> Popayán Cauca, 2020</p>
              </Typography>     
            </div>
            </>
        )
}
}
export default MainConcent

